import { getBookingById } from '../../view/[id]/data';
import { notFound } from 'next/navigation';
import { BookingCheckInCard } from '@/components/bookings/booking-check-in-card';

export default async function BookingCheckInPage(context: { params: Promise<{ id: string }> }) {
  const { id } = await context.params;
  const booking = await getBookingById(id);
  if (!booking) {
    notFound();
  }

  return (
    <BookingCheckInCard booking={booking} />
  );
} 